import * as actionTypes from './actionTypes';
import axios from 'axios';

// Action for Initate
export const leadDataInit = () => {
    return {
        type: actionTypes.LEADBYID_INIT
    };
};

// Action for Scuccess
export const leadDataSuccess = (leadData) => {
    return {
        type: actionTypes.LEADBYID_SUCCESS,
        leadData: leadData
    };
};

// Action for Fail
export const leadDataFail = (error) => {
    return {
        type: actionTypes.LEADBYID_FAIL,
        error: error
    };
};


// Get Leads Data
export const leadData = (leadId,leadcustid) => {
    return dispatch => {
        dispatch(leadDataInit());
        axios.get(`Leads/getById/${leadId}?leadcustid=` + leadcustid)
            .then(res => {
                dispatch(leadDataSuccess(res.data));
            })
            .catch(error => {
                dispatch(leadDataFail(error.response.statusText));
            });
    };
}



