import * as actionTypes from '../actions/actionTypes';
import { updateObject } from '../utility';
const initialState = {
    leadsTableData: null,
    loading: false
};

// for Initate
const leadsTableDataInit = (state, action) => {
    return updateObject(state, { loading: true });
};

// for Success
const leadsTableDataSuccess = (state, action) => {
    return updateObject(state, {
        leadsTableData: action.leadsTableData,
        loading: false
    });
};

// for Fail
const leadsTableDataFail = (state, action) => {
    return updateObject(state, { loading: false });
};


const reducer = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.LEADS_TABLEDATA_INIT: return leadsTableDataInit(state, action);
        case actionTypes.LEADS_TABLEDATA_SUCCESS: return leadsTableDataSuccess(state, action);
        case actionTypes.LEADS_TABLEDATA_FAIL: return leadsTableDataFail(state, action)
        default: return state;
    }
};

export default reducer;


