import * as actionTypes from '../actions/actionTypes';
import { updateObject } from '../utility';

const initialState = {
    companiesTableData: null,
    loading: false
};

// for Initate
const companiesTableDataInit = (state, action) => {
    return updateObject(state, { loading: true });
};

// for Success
const companiesTableDataSuccess = (state, action) => {
    return updateObject(state, {
        companiesTableData: action.companiesTableData,
        loading: false
    });
};

// for Fail
const companiesTableDataFail = (state, action) => {
    return updateObject(state, { loading: false });

};


const reducer = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.COMPANIES_TABLEDATA_INIT: return companiesTableDataInit(state, action);
        case actionTypes.COMPANIES_TABLEDATA_SUCCESS: return companiesTableDataSuccess(state, action);
        case actionTypes.COMPANIES_TABLEDATA_FAIL: return companiesTableDataFail(state, action)
        default: return state;
    }
};

export default reducer;


