import React from 'react';
import {Route,Redirect , Switch} from 'react-router-dom';
import asyncComponent from '../../hoc/asyncComponent/asyncComponent';

const asyncLeads = asyncComponent(() => {
  return import('../../container/Leads/Leads');
});
const asyncLeadDetail = asyncComponent(() => {
    return import('../../container/Leads/leadDetail/LeadsDetail');
  });
  const asyncCompanies = asyncComponent(() => {
    return import('../../container/Companies/Companies');
  });
  const asyncComapnyDetail = asyncComponent(() => {
      return import('../../container/Companies/companyDetail/CompanyDetail');
    });
const LayoutRouting= ()=>{
return  (
   
    <Switch>
    <Route exact path="/home/leads" component={asyncLeads}/>
    <Route exact path="/home/leads/LeadsDetail" component={asyncLeadDetail}/>
    <Route exact path="/home/companies" component={asyncCompanies}/>
    <Route exact path="/home/companies/companyDetail" component={asyncComapnyDetail}/>
     {<Redirect from='/' to='/home/Leads'/>}

         </Switch>
        
)
}
export default LayoutRouting;



