import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import registerServiceWorker from './registerServiceWorker';
import { BrowserRouter } from 'react-router-dom';
import './core/axios.interceptor';
import { Provider } from 'react-redux';
import { createStore, applyMiddleware, combineReducers } from 'redux';
import thunk from 'redux-thunk';
import loginReducer from './store/reducers/login';
import companyReducer from './store/reducers/Company';
import leadReducer from './store/reducers/lead';
import leadDetailReducer from './store/reducers/LeadDetail'

const rootReducer = combineReducers({
  login: loginReducer,
  leadReducer: leadReducer,
  companyReducer: companyReducer,
  leadDetailReducer: leadDetailReducer
});

const store = createStore(rootReducer, applyMiddleware(thunk));


const app = (

  <Provider store={store}>
    <BrowserRouter>
      <App />
    </BrowserRouter>
  </Provider>
);

ReactDOM.render(app, document.getElementById('root'));

registerServiceWorker();



