import * as actionTypes from './actionTypes';
import axios from 'axios';

// Action for Initate
export const leadsDataInit = () => {
    return {
        type: actionTypes.LEADS_TABLEDATA_INIT
    };
};

// Action for Scuccess
export const leadsDataSuccess = (leadsTableData) => {
    return {
        type: actionTypes.LEADS_TABLEDATA_SUCCESS,
        leadsTableData: leadsTableData
    };
};

// Action for Fail
export const leadsDataFail = (error) => {
    return {
        type: actionTypes.LEADS_TABLEDATA_FAIL,
        error: error
    };
};


// Get Leads Data
export const leadsData = (user) => {
    const token =`bearer ${localStorage.getItem('token')}`;
    return dispatch => {
        dispatch(leadsDataInit());
        
        axios.get(`Leads?OnHold=` + `true`, { headers: { Authorization: token } })
            .then(res => {
              
                dispatch(leadsDataSuccess(res.data));
            })
            .catch(error => {
                dispatch(leadsDataFail(error.response.statusText));
            });
    };
}




