import * as actionTypes from './actionTypes';
import axios from 'axios';

// Action for initate
export const companiesDataInit = () => {
    return {
        type: actionTypes.COMPANIES_TABLEDATA_INIT
    };
};

// Action for Success
export const companiesDataSuccess = (companiesTableData) => {
    return {
        type: actionTypes.COMPANIES_TABLEDATA_SUCCESS,
        companiesTableData: companiesTableData
    };
};

// Action for Fail
export const companiesDataFail = (error) => {
    return {
        type: actionTypes.COMPANIES_TABLEDATA_FAIL,
        error: error
    };
};


// Get Companies Data 
export const companiesData = () => {
    return dispatch => {
        dispatch(companiesDataInit());
        axios.get(`Customers/getAll`)
            .then(res => {
                dispatch(companiesDataSuccess(res.data));
            })
            .catch(error => {
                dispatch(companiesDataFail(error.response));
            });
    };
}


