import * as actionTypes from '../actions/actionTypes';
import { updateObject } from '../utility';
const initialState = {
    leadData: null,
    loading: false
};

// for Initate
const leadDataInit = (state, action) => {
    return updateObject(state, { loading: true });
};

// for Success
const leadDataSuccess = (state, action) => {
    return updateObject(state, {
        leadData: action.leadData,
        loading: false
    });
};

// for Fail
const leadDataFail = (state, action) => {
    return updateObject(state, { loading: false });
};


const reducer = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.LEADBYID_INIT: return leadDataInit(state, action);
        case actionTypes.LEADBYID_SUCCESS: return leadDataSuccess(state, action);
        case actionTypes.LEADBYID_FAIL: return leadDataFail(state, action)
        default: return state;
    }
};

export default reducer;


